body {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  margin: 0;
  font-family: "Lato", sans-serif;
  overflow-y: auto;
  height: 100vh;
}

/* Container */
.playground-container {
  width: 100%;
  padding: 50px 20px;
  background-color: rgba(244, 246, 248, 0.9); /* Semi-transparent background to allow background image visibility */
  min-height: 100vh; /* Ensure container fills viewport */
  position: relative; /* Make sure background stays behind */
  z-index: 1; /* Ensure other content is layered above */
}


/* Updated About Section Styles with Centered Content and Padding Between Image and Text */
.about-section {
  padding: 40px 20px; /* Padding around the section */
  display: flex;
  justify-content: center;
}

.about-card {
  background-color: white;
  border-radius: 15px; /* Rounded corners */
  padding: 30px; /* Adjusted padding for compactness */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* Subtle shadow */
  width: 80%; /* Reduced width of the card */
  max-width: 1000px; /* Limiting max width */
  display: flex;
  flex-direction: row; /* Horizontal layout */
  align-items: center; /* Vertically align content */
  justify-content: center; /* Center the content */
}

.about-title {
  font-family: "Righteous", sans-serif;
  font-size: 2.2rem;
  margin: 0;
}

.about-content {
  display: flex;
  flex-direction: row;
  align-items: center; /* Center content vertically */
  justify-content: center; /* Center the content horizontally */
  height: 230px; /* Set height equal to the image height */
  width: 100%;
}

.about-image {
  max-width: 160px; /* Adjusted size of the image */
  height: 230px; /* Match the height with the text container */
  object-fit: cover;
  margin-right: 30px; /* Added space between the image and text */
  border-radius: 10px; /* Slightly rounded corners */
}

.about-text {
  width: 60%; /* Adjusted width of the text to fit inside */
  max-width: 700px; /* Limiting max width */
  line-height: 1.4; /* Adjusted line height for better readability */
  color: #333; /* Text color */
  text-align: left;
  font-family: "Roboto", sans-serif; /* Updated font */
  font-size: 1.1rem; /* Font size */
  display: flex;
  flex-direction: column;
  justify-content: center; /* Vertically center text inside the container */
  margin-top: 0; /* Removed top margin to prevent excessive space */
  padding-left: 10px; /* Added padding to the left of text */
}

.about-space {
  height: 20px;
}








/* Personal Highlights */
.highlights {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.highlight-item {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000); /* Gradient added */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s;
}

.highlight-item:hover {
  transform: scale(1.02);
}

.highlight-title {
  font-size: 1.5rem;
  color: #fff; /* Ensure text is white on dark background */
}

.highlight-description {
  font-size: 1rem;
  color: #ccc;
  margin-top: 10px;
}

/* Skills Section */
.skills-section {
  margin-top: 50px;
}

.skills-title {
  font-family: "Righteous", sans-serif;
  text-align: center;
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 30px;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.skill-card {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000); /* Gradient added */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.skill-card:hover {
  transform: scale(1.02);
}

.skill-name {
  font-size: 1.3rem;
  color: #fff; /* Text color on dark background */
}

.skill-description {
  font-size: 0.9rem;
  color: #ccc; /* Lighter color for description */
  margin-top: 10px;
}

/* Hobbies Section */
.hobbies-section {
  margin-top: 50px;
}

.hobbies-title {
  font-family: "Righteous", sans-serif;
  text-align: center;
  font-size: 2.5rem;
  color: #ffffff;
  margin-bottom: 30px;
}

.hobbies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.hobby-card {
  background: linear-gradient(135deg, #4b0082, #00008b, #000000); /* Gradient added */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.hobby-card:hover {
  transform: scale(1.02);
}

.hobby-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 10px;
}

.hobby-name {
  font-size: 1.3rem;
  color: #fff; /* Text color on dark background */
  margin-top: 15px;
}

.hobby-description {
  font-size: 1rem;
  color: #ccc; /* Lighter color for description */
  margin-top: 10px;
}


